import Vue from 'vue'
import Router from 'vue-router'

import NotFound from './NotFound.vue'

import Composer from './composer/composer.page.vue'
import DocumentEdit from './documentEdit/documentEdit.page.vue'
import Outliner from './outliner/outliner.page.vue'
import Planner from './planner/planner.page.vue'
import Search from './search/search.page.vue'

import userApi from './api/userApi'
import api from './api/api'

Vue.use(Router)

const assertPremium = () =>
  userApi.getUser().then(user => {
    if (user.isPremium) {
      return true
    }
    throw new Error('Can\'t access this route with a Dreamer account.')
  })

const assertOnline = () =>
  api.isOnline().then(
    () => true,
    () => {
      throw new Error('Can\'t access this route while offline.')
    }
  )

/*
We can't use next(false) in any guard because there's no global hook that's called
 when navigation is cancelled. This would leave the app in a 'loading' state forever
 (see App.vue). Instead, we need to use next(error) to cancel navigation.
*/
const compose = (...guards) =>
  function(to, from, next) {
    const guardPromises = guards.map(guard => guard())
    Promise.all(guardPromises).then(
      (...results) => {
        const blocks = results.filter(res => res !== true)
        if (blocks.length === 0) {
          return next()
        }

        const nav = results.filter(res => typeof res === 'object')[0]
        if (nav) {
          return next(nav)
        }

        return next(new Error('Navigation was not allowed.'))
      },
      err => next(err)
    )
  }

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/write'
    },
    {
      path: '/plan',
      name: 'Plan',
      component: Planner
    },
    {
      path: '/outline',
      name: 'Outline',
      component: Outliner
    },
    {
      path: '/write',
      name: 'Write',
      component: Composer
    },
    {
      path: '/analyze',
      name: 'Analyze',
      component: () =>
        import('./analyser/analyser.page.vue' /* webpackChunkName: "analyser" */),
      beforeEnter: compose(assertOnline)
    },
    {
      path: '/export',
      name: 'Export',
      component: () =>
        import('./exporter/exporter.page.vue' /* webpackChunkName: "exporter" */),
      beforeEnter: compose(assertOnline)
    },
    {
      path: '/documentEdit',
      name: 'Document Edit',
      component: DocumentEdit
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/workshop/free-write',
      name: 'Free Write',
      component: () =>
        import('./workshops/freeWrite.page.vue' /* webpackChunkName: "w-free-write" */),
      beforeEnter: compose(
        assertOnline,
        assertPremium
      )
    },
    {
      path: '/workshop/novel-quickstart',
      name: 'Novel Quickstart',
      component: () =>
        import('./workshops/novelQuickstart.page.vue' /* webpackChunkName: "w-novel-quickstart" */),
      beforeEnter: compose(
        assertOnline,
        assertPremium
      )
    },
    {
      path: '/workshop/unblock',
      name: 'Writer\'s Unblock',
      component: () =>
        import('./workshops/writersUnblock.vue' /* webpackChunkName: "w-writers-unblock" */),
      beforeEnter: compose(
        assertOnline,
        assertPremium
      )
    },
    {
      path: '/workshop/plot',
      name: 'Plot Workshop',
      component: () =>
        import('./workshops/plotWorkshop.vue' /* webpackChunkName: "w-plot-workshop" */),
      beforeEnter: compose(
        assertOnline,
        assertPremium
      )
    },
    {
      path: '/workshop/setting',
      name: 'Setting Workshop',
      component: () =>
        import('./workshops/settingWorkshop.vue' /* webpackChunkName: "w-setting-workshop" */),
      beforeEnter: compose(
        assertOnline,
        assertPremium
      )
    },
    {
      path: '*',
      name: 'Page does not exist',
      component: NotFound
    }
  ]
})
